.sc-cover {
  height: 100vh;
  overflow-x: hidden;
  width: 100%;
  display: -moz-flex;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -moz-flex-direction: column;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -moz-justify-content: center;
  -webkit-justify-content: center;
  -ms-justify-content: center;
  justify-content: center;
  background-color: #000;
}

.sc-cover .se-cover-header {
  position: relative;
  z-index: 2;
  padding: 6em 3.5em 3.5em 4em;
}

.sc-cover .se-cover-header h1 {
  color: #fff;
  font-weight: 700;
  line-height: 1em;
  font-size: 2.5em;
  margin: 0 0 0.55em 0;
}

.sc-cover .se-cover-header p {
  color: rgba(255, 255, 255, 0.5);
  position: relative;
  margin: 0 0 1.5em 0;
  font-size: 1em;
  line-height: 1.65em;
}

.sc-cover .se-cover-footer {
  position: relative;
  z-index: 2;
  -moz-transition: opacity 0.5s ease-in-out;
  -webkit-transition: opacity 0.5s ease-in-out;
  -ms-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
  bottom: 4em;
  color: rgba(255, 255, 255, 0.5);
  left: 4em;
  opacity: 0.5;
  position: absolute;
}

.sc-cover .se-cover-footer .se-icons {
  margin: 0;
  cursor: default;
  list-style: none;
  padding-left: 0;
}

.sc-cover .se-cover-footer .se-icons li {
  display: inline-block;
  padding: 0 1em 0 0;
}

.sc-cover .se-cover-footer .se-icons li:last-child {
  padding-right: 0;
}

.sc-cover .se-cover-footer .se-icons li .se-icon:before {
  font-size: 1em;
}

.sc-cover .se-cover-footer .se-icons li a {
  color: inherit;
}

.sc-cover .se-cover-footer .se-copyright {
  font-size: 0.6em;
  list-style: none;
  padding: 0;
}

.sc-cover .se-cover-footer .se-copyright li {
  display: inline-block;
  line-height: 1.3em;
  padding: 0;
}

.sc-cover .se-cover-footer .se-copyright a {
  color: inherit;
}

.sc-cover .se-cover-footer:hover {
  opacity: 1;
}

.sc-cover .se-cover-footer > :last-child {
  margin-bottom: 0;
}

.sc-cover .se-background {
  -moz-transition: opacity 2s ease-in-out;
  -webkit-transition: opacity 2s ease-in-out;
  -ms-transition: opacity 2s ease-in-out;
  transition: opacity 2s ease-in-out;
  height: 100%;
  left: 0;
  opacity: 0.25;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}

.sc-cover .se-background div {
  -moz-transition: opacity 3s ease, visibility 3s;
  -webkit-transition: opacity 3s ease, visibility 3s;
  -ms-transition: opacity 3s ease, visibility 3s;
  transition: opacity 3s ease, visibility 3s;
  background-size: cover;
  background-position: center center;
  height: 100%;
  left: 0;
  opacity: 1;
  position: absolute;
  top: 0;
  width: 100%;
}

.sc-cover .se-start {
  -moz-transition: -moz-transform 0.75s ease, opacity 0.75s ease;
  -webkit-transition: -webkit-transform 0.75s ease, opacity 0.75s ease;
  -ms-transition: -ms-transform 0.75s ease, opacity 0.75s ease;
  transition: transform 0.75s ease, opacity 0.75s ease;
  -moz-transition-delay: 3.5s;
  -webkit-transition-delay: 3.5s;
  -ms-transition-delay: 3.5s;
  transition-delay: 3.5s;
  -moz-transform: translateY(0);
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateY(0);
  border: none;
  bottom: 0;
  color: #fff;
  font-size: 0.8em;
  height: 8.5em;
  left: 50%;
  letter-spacing: 0.1em;
  margin-left: -8.5em;
  opacity: 1;
  outline: 0;
  padding-left: 0.225em;
  position: absolute;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  width: 16em;
  z-index: 1;
}

.sc-cover .se-start:after {
  background-image: url("assets/arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  bottom: 4em;
  content: '';
  display: block;
  height: 1.5em;
  left: 50%;
  margin: 0 0 0 -0.75em;
  position: absolute;
  width: 1.5em;
}

@media only screen and (max-width: 600px) {
  .sc-cover .se-cover-header {
    padding: 0em 2em 0em 2em;
    font-size: 80%;
    bottom: 5em;
  }

  .sc-cover .se-start {
    bottom: 8em;
  }

  .sc-cover .se-cover-footer {
    bottom: 0em;
    left: 0em;
    margin: 1.5em;
  }
}

@media only screen and (max-width: 320px) {
  .sc-cover .se-cover-header {
    font-size: 70%;
  }
}