.se-grid-switcher {
  position: absolute;
  overflow-x: auto;
  overflow-y: hidden;
  height: 50px;
  max-width: 600px;
  background-color: #d4d4d4;
  padding: 0;
  white-space: nowrap;
  width: 100%;
  margin: -25px auto;
  left: 0;
  right: 0;
}

.se-grid-switcher .se-tab {
  display: inline-block;
  text-align: center;
  margin-left: auto;
  left: auto;
  right: auto;
  width: 50%;
  line-height: 48px;
  height: 48px;
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  font-size: 14px;
  text-overflow: ellipsis;
  overflow: hidden;
  cursor: pointer;
}

.se-grid-switcher .se-tab.se-active {
  border-bottom: 2px solid #c3421f;
  font-weight: 600;
}

.se-grid-switcher .se-tab .se-label {
  margin-right: 0.5em;
}