.sc-article .se-big-image {
  /*height: 420px;*/
  position: relative;
  height: 100vh;
  cursor: pointer;
  background-position: center center;
  background-repeat: none;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.sc-article .se-big-image > .se-caption {
  position: absolute;
  bottom: 0px;
  right: 0px;
  color: #fff;
  padding: 1em;
  font-style: italic;
}

.sc-article .se-big-image .se-curtain {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  overflow: hidden;
  opacity: 1;
  background: rgba(0, 0, 0, 0.6);
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.sc-article .se-big-image.se-transparent .se-curtain {
  opacity: 0;
}

.sc-article .se-big-image .se-text {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -o-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.sc-article .se-big-image .se-text .se-next {
  color: white;
  border-bottom: 1px solid white;
  text-decoration: none;
  font-style: italic;
  font-size: 0.8em;
  line-height: 1.5em;
}

.sc-article .se-big-image .se-text .se-title {
  color: white;
  margin: 0;
  margin-top: 0.1em;
  padding-top: 0em;
  padding-bottom: 0em;
  margin-bottom: 0em;
  font-size: 3em;
  line-height: 1.1em;
}

.sc-article .se-big-image .se-text .se-subtitle > h2 {
  color: white;
  margin: 0;
  font-style: italic;
  font-weight: normal;
  margin-top: 0.2em;
  padding-top: 0em;
  padding-bottom: 0em;
  margin-bottom: 0em;
  font-size: 1.5em;
  line-height: 1.2em;
}

.sc-article .se-big-image .se-text .se-subtitle > .se-abstract {
  font-style: normal;
  font-size: 1.2em;
  line-height: 1.3em;
  max-width: 800px;
  margin: 0.7em auto 0;
}

.sc-article .se-content {
  padding: 2em;
  max-width: 600px;
  margin: 0 auto;
}

.sc-article .se-content h1 {
  margin-top: 0em;
  padding-top: 0em;
  padding-bottom: 0em;
  margin-bottom: 0.24em;
  font-size: 2.5em;
  line-height: 1.08em;
  color: #333;
}

.sc-article .se-content h2 {
  font-weight: normal;
  font-style: italic;
  font-size: 1.5em;
  line-height: 1.2em;
  margin-bottom: 0.4em;
  color: #333;
}

.sc-article .se-content p {
  margin-bottom: 20px;
}

.sc-article .se-content blockquote {
  padding-left: 20px;
  border-left: 2px solid #333;
  margin: 1em 0 2em;
  font-style: italic;
}

.sc-article.se-fade-up {
  opacity: 0;
  -webkit-transform: scale(0.8) translate3d(0, -10%, 0);
  -moz-transform: scale(0.8) translate3d(0, -10%, 0);
  -ms-transform: scale(0.8) translate3d(0, -10%, 0);
  -o-transform: scale(0.8) translate3d(0, -10%, 0);
  transform: scale(0.8) translate3d(0, -10%, 0);
  -webkit-transition: all 450ms cubic-bezier(0.165, 0.84, 0.44, 1);
  -moz-transition: all 450ms cubic-bezier(0.165, 0.84, 0.44, 1);
  -o-transition: all 450ms cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 450ms cubic-bezier(0.165, 0.84, 0.44, 1);
}

.sc-article.se-easing-upward {
  -webkit-transition: all 450ms cubic-bezier(0.165, 0.84, 0.44, 1);
  -moz-transition: all 450ms cubic-bezier(0.165, 0.84, 0.44, 1);
  -o-transition: all 450ms cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 450ms cubic-bezier(0.165, 0.84, 0.44, 1);
}

.sc-article .se-ref-card {
  position: relative;
  background-color: #fff;
  border-radius: 6px;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0,0,0,.1);
  display: -webkit-box;
  display: flex;
  margin: 2em 0 0;
  cursor: pointer;
}

.sc-article .se-ref-card .se-card-photo {
  height: 100%;
  width: 8em;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  background-position: center center;
  background-repeat: none;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.sc-article .se-ref-card .se-card-content {
  font-size: 0.8em;
  line-height: 1.4em;
  margin: 1em 1em 1em 11em;
  -webkit-box-flex: 1;
  flex: 1;
}

.sc-article .se-ref-card .se-card-content .se-card-heading {
  font-weight: bold;
  margin-bottom: 0.5em;
  margin-right: 2.5em;
}

.sc-article .se-ref-card .se-card-content .se-card-text {
  margin: 0;
}

.sc-article .se-ref-card .se-card-content .se-icon {
  position: absolute;
  font-size: 2em;
  right: 0.4em;
}
