.sc-image-grid {
  -moz-transition: all .5s ease-in-out;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  -moz-column-gap: 1em;
  -webkit-column-gap: 1em;
  column-gap: 1em;
  -moz-column-fill: initial;
  -webkit-column-fill: initial;
  column-fill: initial;
  column-count: 4;
  background-color: #000;
  padding: 3em 1em 1em 1em;
}

.sc-image-grid.se-guterless {
  -moz-column-gap: 0;
  -webkit-column-gap: 0;
  column-gap: 0;
  padding: 0;
}

.sc-image-grid .se-grid-item {
  position: relative;
  color: #fff;
  background-color: #000;
  width: 100%;
  margin-bottom: 1em;
  cursor: pointer;
}

.sc-image-grid.se-guterless .se-grid-item {
  margin-bottom: 0;
}

.sc-image-grid .se-grid-item img {
  -moz-transition: all .5s ease-in-out;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  max-width: 100%;
  vertical-align: top;
  opacity: 0.3;
}

.sc-image-grid .se-grid-item img:hover {
  opacity: 0.7;
}

.sc-image-grid .se-grid-item .se-title {
  position: absolute;
  top: 0;
  margin: 1em;
  font-size: 1.3em;
}

.sc-image-grid .se-grid-item .se-topic {
  position: absolute;
  bottom: 0;
  margin: 1em 1.7em;
  font-size: 0.8em;
  line-height: 1.4em;
}

@media only screen and (max-width: 600px) { 
  .sc-image-grid {
    column-count: 1!important;
  }
}

@media only screen and (max-width: 1024px) { 
  .sc-image-grid {
    column-count: 2;
    padding: 3em 0 0 0;
    -moz-column-gap: 0em;
    -webkit-column-gap: 0em;
    column-gap: 0em;
  }

  .sc-image-grid .se-grid-item {
    margin-bottom: 0;
  }
}