.sc-menu {
  position: fixed;
  width: 100%;
  z-index: 10;
  background: rgba(0, 0, 0, 0.45);
  height: 3em;
}

.sc-menu .se-logo {
  position: absolute;
  width: 100%;
  top: 0.5em;
  left: 0.5em;
  height: 2em;
  opacity: 0.8;
  cursor: pointer;
  -moz-transition: opacity 0.5s ease-in-out;
  -webkit-transition: opacity 0.5s ease-in-out;
  -ms-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}

.sc-menu .se-logo:hover {
  opacity: 1;
}

.sc-menu .se-logo img {
  height: 100%;
  float: left;
}

.sc-menu .se-logo h1 {
  height: 2em;
  color: #fff;
  line-height: 2em;
  padding: 0 1em 0 3.4em;
}

.sc-menu ul {
  margin: 0;
  list-style: none;
  font-size: 0.8em;
  font-weight: 700;
  height: 2em;
  letter-spacing: 0.1em;
  line-height: 2em;
  position: absolute;
  right: 0.9em;
  top: 0.9em;
}

.sc-menu .se-menu-item {
  display: inline-block;
  height: 2em;
  line-height: 2em;
  padding: 0 1em;
  margin-left: 1em;
  border-radius: 6px;
  background-color: transparent;
  box-shadow: inset 0 0 0 1px #999;
  color: #fff;
  -moz-appearance: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  -moz-transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
  -webkit-transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
  -ms-transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
  border-radius: 6px;
  border: 0;
  font-weight: 400;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  cursor: pointer;
}

.sc-menu .se-menu-item.se-active,
.sc-menu .se-menu-item:hover {
  background-color: rgba(153, 153, 153, 0.25);
}

.sc-menu .se-menu-item .se-label {
  margin-right: 0.5em;
}

.sc-menu .se-menu-item.se-donate {
  background: #f04e23;
}

.sc-menu .se-menu-item.se-donate a {
  border-bottom: none;
}

@media only screen and (max-width: 600px) {
  .sc-menu .se-logo h1 {
    display: none;
  }

  .sc-menu .se-menu-item.se-donate .se-label {
    display: none;
  }
}

@media only screen and (max-width: 370px) {
  .sc-menu .se-menu-item .se-label {
    display: none;
  }
}
